@font-face {
  font-family: 'Geometria';
  src: url("/fonts/geometria-medium.ttf") format("ttf"), url("/fonts/geometria-medium.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Geometria', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.header__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  padding-top: 20px;
  padding-bottom: 36px; }

.header__svg {
  fill: #8577bb;
  width: 53px;
  height: 55px;
  margin: 0;
  margin-right: 15px; }

.header__title {
  font-size: 36px;
  font-family: 'Bad Script';
  color: #000;
  line-height: 0.667;
  text-decoration: none; }

.header__contacts {
  display: flex;
  list-style: none;
  flex-wrap: wrap; }

.header__menu {
  margin: 0;
  padding: 0; }

.header__menu {
  background-color: #413471; }

@media (max-width: 768px) {
  .header__wrap {
    justify-content: center;
    padding-bottom: 13px; } }

.contacts__svg {
  fill: #d3d3d0;
  width: 25px;
  height: 21px;
  margin: 0;
  margin-right: 15px; }

.contacts__tel {
  font-size: 16px;
  font-family: 'Geometria';
  color: #000;
  line-height: 1.5;
  text-decoration: none;
  margin-right: 50px; }

.contacts__address {
  font-size: 16px;
  font-family: 'Geometria';
  color: #000;
  line-height: 1.5;
  text-decoration: none;
  margin-right: 35px; }

@media (max-width: 510px) {
  .header__contacts {
    justify-content: space-around; }
  .contacts__address {
    margin-right: 10px; }
  .contacts__tel {
    margin-right: 10px; } }

.menu__wrap {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: space-around;
  align-items: center; }

.menu__element:first-child {
  margin-left: 45px; }

.menu__link {
  color: #fff;
  text-decoration: none;
  font-size: 15px;
  font-family: "Geometria";
  line-height: 1.6;
  display: block;
  padding: 1em;
  transition-duration: 500ms; }
  .menu__link:hover {
    background: rgba(0, 0, 0, 0.3); }

.menu__svg {
  fill: #fff;
  width: 14px;
  height: 15px;
  margin-right: 15px; }

.menu__button {
  background: #6d5cad;
  width: 218px;
  height: 43px;
  border-radius: 25px;
  border: none;
  font-size: 15px;
  color: #fff;
  display: block;
  cursor: pointer;
  box-shadow: none;
  margin-right: 35px; }
  .menu__button:hover {
    background: linear-gradient(to top, #01abce, #7ac398); }

.menu__hamburger {
  width: 45px;
  height: 32px;
  margin-right: auto;
  display: none;
  border: none;
  background-color: transparent;
  appearance: none;
  cursor: pointer;
  padding-top: 5px;
  padding-left: 5px; }
  .menu__hamburger::after, .menu__hamburger::before {
    height: 5px;
    display: block;
    background-color: #fff;
    content: ''; }
  .menu__hamburger span {
    height: 5px;
    display: block;
    background-color: #fff;
    margin: 8px 0; }

@media (max-width: 940px) {
  .menu {
    padding-bottom: 5px; } }

@media (max-width: 768px) {
  .menu__wrap {
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    margin: 0; }
  .menu__element:first-child {
    margin-left: 0; }
  .menu__hamburger {
    display: block;
    outline: none; }
  .menu__link {
    padding: 10px 0;
    display: none; }
    .menu__link:hover {
      background-color: transparent; }
  .menu__button {
    display: none;
    margin: 0; }
  .menu--active .menu__hamburger span {
    display: none; }
  .menu--active .menu__hamburger::after {
    transform: rotate(45deg); }
  .menu--active .menu__hamburger::before {
    transform: rotate(-45deg) translate(-3px, 3px); }
  .menu--active .menu__link {
    display: block; }
  .menu--active .menu__button {
    display: block; } }

.slider__wrap {
  padding-top: 55px;
  padding-bottom: 55px; }

.slider__images {
  position: relative;
  max-height: 489px;
  width: 1140px;
  object-fit: contain; }

.slider__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
  transition: 1s; }

.slider__img--showing {
  opacity: 1;
  z-index: 2; }
